import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs'; 

import { Businesses } from '../models/businesses';

@Injectable({providedIn: "root"})
export class SharedService {

    private isActive = new Subject<boolean>();
    private isListView = new Subject<boolean>();

    private businessResults = new Subject;

    getState$ = this.isActive.asObservable();
    getView$ = this.isListView.asObservable();

    getResults$ = this.businessResults.asObservable();

    setState(state: boolean) {
        this.isActive.next(state);
    }

    setView(state: boolean) {
        this.isListView.next(state);
    }

}