  houseKeeping(mapView) {
    mapView.when(() => {
      console.log('mapView ready: ', mapView.ready);
      this._loaded = mapView.ready;
    });
  }

  ngOnInit() {
    this.initializeMap().then((mapView) => {
      this.houseKeeping(mapView);
    })
  }

  showCard() {
    this.clicked = !this.clicked;
  }

  closeCard() {
    this.clicked = !this.clicked;
  }
