import { Component, OnInit, Output, EventEmitter } from '@angular/core';

import { SharedService } from '../services/shared.service';
import { Subscription} from 'rxjs';

@Component({
  selector: 'coc-mobile-controls',
  templateUrl: './mobile-controls.component.html'
})

export class MobileControlsComponent implements OnInit {
  isActive: boolean = false;
  isListView: boolean; 

  constructor(public sharedService: SharedService) { 
    sharedService.getState$.subscribe(
      isActive => {
        this.isActive = isActive;
      }
    )

    sharedService.getView$.subscribe(
      isListView => {
        this.isListView = isListView;
      }
    )
  }

  ngOnInit() {
  }

  toggleListView() {
    this.sharedService.setView(!this.isListView);
  }

  toggleActive() {
    this.sharedService.setState(!this.isActive);
  }

}
